import React from 'react'
import Typed from 'typed.js'

class Header extends React.Component {
  componentDidMount() {
    const strings = ["React developer.", "UX obsessive.", "Design geek.", "Tutor and resource creator."]

    const options = {
      strings,
      typeSpeed: 50,
      backSpeed: 50,
      loop: true,
      loopCount: Infinity,
    }
    this.typed = new Typed(this.el, options)
  }

  componentWillUnmount() {
    this.typed.destroy()
  }

  render() {
    return (
      <header>
        <h1>&lt;<span>JoanneImlay</span> /&gt;</h1>
        <div className="type-wrap">
          <span
            style={{ whiteSpace: 'pre' }}
            ref={(el) => { this.el = el }}
          />
        </div>
        <div className="video-hero">
          <video autoPlay="true" width="100%" loop muted>
            <source src="hero.ogv" />
            <source src="hero.webm" />
          </video>
          <div className="icons-hero">
            <a href='mailto:jo.imlay@gmail.com'><span className="icon"><i className="fas fa-envelope-square" aria-hidden="true" /></span></a>
            <a href='https://www.linkedin.com/in/joanne-imlay-4a7a7056/' target="_blank" rel="noopener noreferrer"><span className="icon"><i className="fab fa-linkedin" aria-hidden="true" /></span></a>
            <a href="https://www.github.com/dentednerd" target="_blank" rel="noopener noreferrer"><span className="icon"><i className="fab fa-github-square" aria-hidden="true" /></span></a>
            <a href="https://www.twitter.com/dentednerd" target="_blank" rel="noopener noreferrer"><span className="icon"><i className="fab fa-twitter-square" aria-hidden="true" /></span></a>
          </div>
        </div>
      </header>
    )
  }
}

export default Header


